









































import { Vue, Component, Prop, Provide } from "vue-property-decorator";
import Home from "@/views/Home.vue";
import MenuVue from "@/components/Menu.vue";
import Experience from "@/views/Jobs.vue";
import About from "@/views/About.vue";
import Projects from "@/views/Projects.vue";
import Contact from "@/views/Contact.vue";

@Component({
  components: {
    home: Home,
    experience: Experience,
    about: About,
    projects: Projects,
    contact: Contact,
    "header-menu": MenuVue
  }
})
export default class AppVue extends Vue {
  options: any = {
    menu: "#header-menu",

    anchors: ["home", "about", "experience", "portfolio", "contact"],
    onLeave: this.onLeave,
    afterLoad: this.afterLoad
    // fitToSection: false
  };
  isMenuVisible: boolean = false;

  onMoveDown() {
    (<any>this.$refs.fullpage).api.moveSectionDown();
  }

  afterLoad(origin: any, destination: any, direction: any) {
    if (destination.index == 0) {
      (<HTMLElement>document.querySelector(".header-menu")).classList.add(
        "hide"
      );
    } else {
      (<HTMLElement>document.querySelector(".header-menu")).classList.remove(
        "hide"
      );
    }

    // if (destination.index == 1) {
    //   document.querySelectorAll(".badge").forEach((el: Element) => {
    //     el.classList.add("flip-in-hor-bottom");
    //   });
    // }
  }

  onLeave(origin: any, destination: any, direction: any) {
    if (origin.index == 1) {
      (<HTMLElement>document.querySelector(".header-menu")).classList.add(
        "header-fixed"
      );
    }

    if (destination.index == 1) {
      (<HTMLElement>document.querySelector(".header-menu")).classList.remove(
        "header-fixed"
      );
    }

    if (destination.index == 0) {
      (<HTMLElement>document.querySelector(".header-menu")).classList.add(
        "hide"
      );
    }
  }
}
